<template>
<div class="body"
  oncontextmenu="return false"
  @dragenter="listen__x__ondragenter"
  @dragleave="listen__x__ondragleave"
  @dragover="listen__x__ondragover"
  @drop="listen__x__ondrop"
  >
  <tool-bar ref="tool-bar" :style="{flex: '0 0 124px'}"
    @onfilechange="listen__tool_bar__onfilechange"
    @onfileerror="listen__tool_bar__onfileerror" />
  <div class="sub-body" :style="{flex: '1 0 0'}">
    <image-view ref="image-view"
      :style="{flex: '2 0 0'}"
      :frame-data="$store.state.frameData"/>
    <result-view :style="{flex: '1 0 0'}"/>
  </div>
  <div>
    <usage-dialog />
  </div>
  <md-snackbar :md-position="'left'" :md-duration="2000" :md-active.sync="$store.state.showSnackbar" md-persistent>
    <span>{{ $store.state.messageSnackbar }}</span>
  </md-snackbar>
  <div v-if="$store.state.showLayer" class="mlayer">
    <div>
      <div class="w-spinner"><div class="spinner"></div></div>
      <span>{{ $store.state.messageLayer }}</span>
    </div>
  </div>
  <div v-if="$store.state.counterDrag > 0" class="m-drag-layer">
    <div>
      <span>Drop here</span>
    </div>
  </div>
</div>
</template>

<script>
import ToolBar from './views/ToolBar.vue'
import ImageView from './views/ImageView.vue'
import ResultView from './views/ResultView.vue'
import UsageDialog from './components/UsageDialog.vue'

import PARSE from '@/js/parse.js'
import MISC from '@/js/miscellaneous.js'

import recognize from '@/js/recognize.js'

export default {
  name: 'App',
  data: function () {
    return {
    }
  },
  components: {
    ToolBar,
    ImageView,
    ResultView,
    UsageDialog
  },
  methods: {
    listen__x__ondragenter: function (e) {
      e.preventDefault()
      this.$store.commit('counterDrag', {op: 'increment'});
    },
    listen__x__ondragleave: function (e) {
      e.preventDefault()
      this.$store.commit('counterDrag', {op: 'decrement'});
    },
    listen__x__ondragover: function (e) {
      e.preventDefault()
    },
    listen__x__ondrop: function (e) {
      e.preventDefault()
      this.$store.commit('counterDrag', {op: 'clear'});
      this.$refs['tool-bar'].listen__file__onchange(e.dataTransfer.files);
    },
    listen__tool_bar__onfilechange: function (file) {
      const Vue = this
      
      if (Vue.$store.state.frameData._empty == false) {
        Vue.$store.commit('frameData', {value: {
          id: MISC.getUuid4(),
          _empty: true,
          pred: []
        }});
      }
      Vue.$store.commit('showLayer', {value: true});
      
      Vue.doubleRaf(async () => {
        let image;
        Vue.$store.commit('messageLayer', {value: 'parse image...'});

        let tokens = file.name.split('.');
        let ext = tokens[tokens.length - 1].toLowerCase();
        try {
          image = await PARSE.parseImage(file, ext);
        } catch (err) {
          console.log(err)
          Vue.showSnackbar({message: "image parsing failure"})
          Vue.$refs['tool-bar'].$refs.file.clearField();
          Vue.$store.commit('showLayer', {value: false});
          return
        }
        let cornerstoneImage = await Vue.$cornerstone.createCornerstoneImageRgba(undefined, image.pixelData, image.width, image.height);

        Vue.$store.commit('messageLayer', {value: 'recognize...'});
        let pred;
        try {
          pred = await recognize.recognize(file);
          console.log(pred);
        } catch (err) {
          console.log(err)
          Vue.showSnackbar({message: err})
          Vue.$refs['tool-bar'].$refs.file.clearField();
          Vue.$store.commit('showLayer', {value: false});
          return
        }
        Vue.$store.commit('frameData', {value: {
          id: MISC.getUuid4(),
          name: file.name,
          cornerstoneImage,
          _empty: false,
          pred
        }});
        Vue.$store.commit('showLayer', {value: false});
      });
    },
    listen__tool_bar__onfileerror: function (param) {
      console.log(param);
      const Vue = this;
      if (Vue.$store.state.frameData._empty == false) {
        Vue.$store.commit('frameData', {value: {
          id: MISC.getUuid4(),
          _empty: true,
          pred: []
        }});
      }
      this.showSnackbar(param);
    },
    showSnackbar: function (param) {
      this.$store.commit('messageSnackbar', {value: param.message});
      this.$store.commit('showSnackbar', {value: true});
      this.$store.commit('showLayer', {value: false});
    },
    doubleRaf: function (callback) {
      requestAnimationFrame(() => {
        requestAnimationFrame(callback)
      })
    },
    fetchBlob: function (url) {
      return new Promise(function(resolve) {
        fetch(url)
          .then(response => response.blob())
          .then(res => {
            resolve(res)
          })
      })
    },
    sleep: function (ms) {
      return new Promise((r) => setTimeout(r, ms))
    }
  },
  async mounted () {
    const Vue = this
    Vue.$store.commit('showLayer', {value: true});
    
    Vue.doubleRaf(async () => {
      Vue.$store.commit('messageLayer', {value: 'demo using stub data...'});

      let image;
      try {
        const blob = await this.fetchBlob('https://live.staticflickr.com/288/32317432425_69f0f7536f_c_d.jpg');
        image = await PARSE.parseImage(blob, 'jpg');
      } catch (err) {
        Vue.showSnackbar({message: "fail to get flickr image"})
        Vue.$refs['tool-bar'].$refs.file.clearField();
        Vue.$store.commit('showLayer', {value: false});
        return
      }
      let cornerstoneImage = await Vue.$cornerstone.createCornerstoneImageRgba(undefined, image.pixelData, image.width, image.height);

      let pred = JSON.parse('[{"id":"0","text":"Cruis","prob":0.9962915182113647,"active":true,"highlight":false,"points":[{"x":458,"y":166},{"x":518,"y":120},{"x":537,"y":144},{"x":478,"y":190}]},{"id":"1","text":"Jungle","prob":0.917414665222168,"active":true,"highlight":false,"points":[{"x":386,"y":220},{"x":449,"y":169},{"x":469,"y":193},{"x":405,"y":244}]},{"id":"2","text":"Market Oisice","prob":0.9108983278274536,"active":true,"highlight":false,"points":[{"x":132,"y":231},{"x":343,"y":286},{"x":336,"y":312},{"x":125,"y":257}]},{"id":"3","text":"Station","prob":0.9994226098060608,"active":true,"highlight":false,"points":[{"x":448,"y":243},{"x":543,"y":184},{"x":561,"y":213},{"x":467,"y":272}]},{"id":"4","text":"Train","prob":0.9985295534133911,"active":true,"highlight":false,"points":[{"x":396,"y":282},{"x":452,"y":242},{"x":468,"y":265},{"x":412,"y":305}]},{"id":"5","text":"TicketBox","prob":0.9949602484703064,"active":true,"highlight":false,"points":[{"x":400,"y":346},{"x":530,"y":279},{"x":546,"y":309},{"x":416,"y":376}]},{"id":"6","text":"Market","prob":0.9998836517333984,"active":true,"highlight":false,"points":[{"x":213,"y":393},{"x":327,"y":373},{"x":331,"y":398},{"x":217,"y":418}]},{"id":"7","text":"ChairLift","prob":0.9827647805213928,"active":true,"highlight":false,"points":[{"x":405,"y":416},{"x":530,"y":359},{"x":547,"y":394},{"x":421,"y":451}]},{"id":"8","text":"Cariblcan","prob":0.877497673034668,"active":true,"highlight":false,"points":[{"x":262,"y":427},{"x":335,"y":414},{"x":338,"y":432},{"x":265,"y":444}]}]');
      Vue.$store.commit('frameData', {value: {
        id: MISC.getUuid4(),
        name: 'stub.png',
        cornerstoneImage,
        _empty: false,
        pred
      }});
      this.$refs['tool-bar'].fileName = 'stub.data'
      await this.sleep(2000);
      Vue.$store.commit('showLayer', {value: false});
    });
  }
}
</script>

<style>
#app {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding: 0;
  margin: 0;
  overflow: auto;
  background: rgb(239, 239, 239);
}

.body {
  display:flex;
  flex-direction:column;
  height:100%;
  width:100%;
}

.body > .sub-body {
  display:flex;
  flex-direction:row;
  overflow: hidden;
}

div {
  position: relative;
}

div.mlayer {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.75);
  align-items: center;
  justify-content: center;
  z-index: 99;
}
div.mlayer > div,
div.mlayer > span {
  margin-bottom: 10px;
  font-size: 14px;
}
div.m-drag-layer {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.75);
  align-items: center;
  justify-content: center;
  z-index: 98;
  pointer-events: none;
}
div.m-drag-layer > div,
div.m-drag-layer > span {
  margin-bottom: 10px;
  font-size: 50px;
}
.w-spinner {
  position:relative;
  display:inline-block;
  top: 7px;
  width:20px;
  height:20px;
  margin-right: 8px;
}
@keyframes spinner {
  to {transform: rotate(360deg);}
}
.spinner {
  position:relative;
  width: 100%;
  height: 100%;
}
.spinner:before {
  content: '';
  box-sizing: border-box;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 3px solid #999;
  border-top-color: #000;
  animation: spinner .6s linear infinite;
}
::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

::-webkit-scrollbar
{
	width: 6px;
	background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #AAA;
}
</style>
